___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: 'Arial', sans-serif;
    height: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    background-color: rgb(221, 221, 221); 
}

.aboutme .content-wrapper {
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 32px;
    background-color: rgb(56, 56, 56); 
}

.aboutme .content-wrapper img {
  margin-right: -40px;   
  z-index: 2;            /* ensures image is above the text box */
  box-shadow: 0 4px 16px rgba(0,0,0,0.18); 
  border: 1px solid #000000;     
  border-radius: 10px;            
}

.aboutme-text {
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255); 
  border: 1px solid #e0e0e0;     
  border-radius: 10px;            
  box-shadow: 0 2px 8px rgba(0,0,0,0.10); 
  padding: 24px;
  transition: box-shadow 0.2s;
}

.content-wrapper {
    padding: 50px;
    display: flex;  
    flex-direction: column;
    align-items: center;
}

.contacts-row {
  display: flex;
  align-items: center;
  gap: 8px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px; 
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.project-item {
  background: #fff;
  border: 1px solid #e0e0e0;     
  border-radius: 10px;            
  box-shadow: 0 2px 8px rgba(0,0,0,0.10); 
  padding: 24px;
  transition: box-shadow 0.2s;
}

.project-item:hover {
  box-shadow: 0 4px 16px rgba(0,0,0,0.18); 

}

.project-image {
  width: 100%;
  max-width: 600px;
  border-radius: 8px;
  margin-bottom: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.10);  
}

.icons-aboutme {
  display: flex;
  gap: 12px;
  margin-top: 16px;
  align-self: flex-end;
}

.icons-aboutme a {
  text-decoration: none;
  color: #000000;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.project-item-header {
  display: flex;
  gap: 12px;
  align-items: center;
  justify-content: space-between; 
}

.icons-projects a{
  text-decoration: none;
  color: #000000; 
  font-size: 1.5rem;
}

.icons-projects svg{
  width: 1.5rem;
  height: 1.5rem;
  fill: #000000; 
}

.icons-footer a {
  text-decoration: none;
  color: #ffffff; 
  font-size: 2rem
}

footer {
  background-color: rgb(56, 56, 56); 
  color: #ffffff;
}

footer svg {
   fill: #ffffff;
}

footer .copyright {
  text-align: center; 
}

/* Tablet styles */
@media (max-width: 900px) {
  .projects-grid {
    grid-template-columns: repeat(2, 1fr);
    max-width: 100%;
  }
  .content-wrapper {
    padding: 24px;
  }
  .aboutme .content-wrapper {
    flex-direction: column;
    align-items: center;
    gap: 24px;
  }
  .aboutme .content-wrapper img {
    margin-right: 0;
    margin-bottom: -40px;
  }
  .aboutme-text {
    padding-left: 0;
  }
}

/* Mobile styles */
@media (max-width: 600px) {
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  .content-wrapper {
    padding: 12px;
  }
  .aboutme .content-wrapper {
    flex-direction: column;
    align-items: center;
    gap: 16px;
  }
  .aboutme .content-wrapper img {
    width: 180px;
    height: 180px;
    margin-right: 0;
    margin-bottom: -24px;
  }
  .aboutme-text {
    padding: 16px;
    font-size: 1rem;
  }
  .project-image {
    max-width: 100%;
    height: auto;
  }
  .project-item {
    padding: 16px;
    min-height: unset;
  }
  footer {
    padding: 16px 0;
    font-size: 1rem;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,gCAAgC;IAChC,YAAY;IACZ,SAAS;IACT,UAAU;IACV,aAAa;IACb,sBAAsB;IACtB,oCAAoC;AACxC;;AAEA;IACI,mBAAmB;IACnB,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,iCAAiC;AACrC;;AAEA;EACE,mBAAmB;EACnB,UAAU,aAAa,wCAAwC;EAC/D,uCAAuC;EACvC,yBAAyB;EACzB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,oCAAoC;EACpC,yBAAyB;EACzB,mBAAmB;EACnB,sCAAsC;EACtC,aAAa;EACb,2BAA2B;AAC7B;;AAEA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,QAAQ;AACV;;AAEA;EACE,aAAa;EACb,qCAAqC;EACrC,SAAS;EACT,WAAW;EACX,iBAAiB;EACjB,cAAc;AAChB;;AAEA;EACE,gBAAgB;EAChB,yBAAyB;EACzB,mBAAmB;EACnB,sCAAsC;EACtC,aAAa;EACb,2BAA2B;AAC7B;;AAEA;EACE,uCAAuC;;AAEzC;;AAEA;EACE,WAAW;EACX,gBAAgB;EAChB,kBAAkB;EAClB,mBAAmB;EACnB,sCAAsC;AACxC;;AAEA;EACE,aAAa;EACb,SAAS;EACT,gBAAgB;EAChB,oBAAoB;AACtB;;AAEA;EACE,qBAAqB;EACrB,cAAc;EACd,eAAe;EACf,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,SAAS;EACT,mBAAmB;EACnB,8BAA8B;AAChC;;AAEA;EACE,qBAAqB;EACrB,cAAc;EACd,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,cAAc;EACd,aAAa;AACf;;AAEA;EACE,qBAAqB;EACrB,cAAc;EACd;AACF;;AAEA;EACE,iCAAiC;EACjC,cAAc;AAChB;;AAEA;GACG,aAAa;AAChB;;AAEA;EACE,kBAAkB;AACpB;;AAEA,kBAAkB;AAClB;EACE;IACE,qCAAqC;IACrC,eAAe;EACjB;EACA;IACE,aAAa;EACf;EACA;IACE,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;EACX;EACA;IACE,eAAe;IACf,oBAAoB;EACtB;EACA;IACE,eAAe;EACjB;AACF;;AAEA,kBAAkB;AAClB;EACE;IACE,0BAA0B;IAC1B,SAAS;EACX;EACA;IACE,aAAa;EACf;EACA;IACE,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;EACX;EACA;IACE,YAAY;IACZ,aAAa;IACb,eAAe;IACf,oBAAoB;EACtB;EACA;IACE,aAAa;IACb,eAAe;EACjB;EACA;IACE,eAAe;IACf,YAAY;EACd;EACA;IACE,aAAa;IACb,iBAAiB;EACnB;EACA;IACE,eAAe;IACf,eAAe;EACjB;AACF","sourcesContent":["body {\n    font-family: 'Arial', sans-serif;\n    height: 100%;\n    margin: 0;\n    padding: 0;\n    display: flex;\n    flex-direction: column;\n    background-color: rgb(221, 221, 221); \n}\n\n.aboutme .content-wrapper {\n    flex-direction: row;\n    align-items: center;\n    justify-content: center;\n